@import url('https://fonts.googleapis.com/css2?family=Inter:wght@500&family=Karla:wght@700&display=swap');

html,
body {
    font-size: 12.5px;
    color: #293264;
    margin: 0;
    padding: 0;
    height: 100vh;
    min-height: -webkit-fill-available;
    height: -webkit-fill-available;

    @media (max-width: 420px) {
        font-size: 11px;
    }
}

#root,
.main,
.wrapper {
    height: inherit;
}

.title {
    font-family: 'Karla', sans-serif;

    &--1 {
        font-size: 4rem;
        margin: 0;
    }
}

.button_set {
    display: flex;
    gap: 1.1rem;
}

.button {
    border: 1px solid #4D5B9E;
    background: none;
    border-radius: 1rem;
    color: #293264;
    padding: 0.3rem 0.7rem;
    font-size: 0.9rem;
    font-family: 'Inter', sans-serif;

    @media (max-width: 420px) {
        min-height: 4.1rem;
        min-width: 4.1rem;
    }

    &--control {
        background: #4D5B9E;
        color: #fff;
        padding: 1.45rem 2.5rem;
        transition: .3s;
        font-size: 1.3rem;

        @media (max-width: 420px) {
            min-height: 5.5rem;
            min-width: 5.5rem;
            padding: 0 2.5rem;
        }
    }

    &:active {
        transform: scale(1.05);
        transition: .3s;
    }
}

.wrapper {
    max-width: fit-content;
    margin: 0 auto;
    width: 100%;
    width: calc(100% - 3rem);
    flex-direction: column;
    display: flex;
}

.question {
    padding: 1.4rem 0;
    border-bottom: 1px solid #DBDEF0;
}

.start_menu {
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: inherit;

}

.difficulty {
    position: relative;

    width: 100%;
    height: 3rem;
    margin-bottom: 1.5rem;


    &__menu {
        width: inherit;
        height: inherit;
        font-family: 'Karla', sans-serif;
        padding: 0 1rem;
        font-size: 1.3rem;
        border: 1px solid #293264;
        color: #293264;

        border-radius: 1.3rem;
        appearance: none !important;
        -webkit-appearance: none !important;
        -moz-appearance: none !important;
    }


    &::after {
        content: "â–¼";
        color: #4D5B9E;
        font-size: 1rem;
        top: 50%;
        transform: translateY(-50%);
        right: 1rem;
        position: absolute;
    }
}

.control-set {
    margin: 2rem 0 3rem 0;
    display: flex;
    gap: 1.1rem;
    align-items: center;
    justify-content: center;
}